<template>
  <div style="background:white;height:100%;">
    <div class="SettingHeader">
      <mu-appbar style="width: 100%;" :color="$store.state.color" class="header">
        <a @click="alertTo">
          <mu-button icon slot="left" class="icon" size="20">
            <mu-icon value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
        <span>录入车辆</span>
      </mu-appbar>
    </div>
    <div class="settingContent">
      <div class="flex">
        <div class="title">
          <span>车辆编号</span>
        </div>
        <div class="input">
          <input type="text" id="input" v-model="name" placeholder="请输入车辆编号" />
        </div>
      </div>
      <div class="flex">
        <div class="title">
          <span>车牌号</span>
        </div>
        <div class="input">
          <input type="text" id="input" v-model="number" placeholder="请输入车辆车牌号" />
        </div>
      </div>
      <div class="flex">
        <div class="title">
          <span>驾驶人姓名</span>
        </div>
        <div class="input">
          <input type="text" id="input" v-model="carPerson" placeholder="请输入驾驶人姓名" />
        </div>
      </div>
      <div class="flex">
        <div class="title">
          <span>联系方式</span>
        </div>
        <div class="input">
          <input type="text" id="input" v-model="carPhone" placeholder="请输入您的联系方式" />
        </div>
      </div>
      <div class="flex">
        <div class="title">
          <span>监测因子</span>
        </div>
        <div class="input" @click="bottomTo">
          <input type="text" id="input" v-model="result" disabled placeholder="请选择您车辆监测的因子" />
        </div>
      </div>
      <div class="submit" :style="'background:'+$store.state.color" @click="confirmSubmit()">
        <span>录入</span>
      </div>
    </div>
    <mu-container>
      <mu-bottom-sheet :open.sync="open">
        <mu-list @item-click="closeBottomSheet">
          <!-- <mu-sub-header class="bottomBorder">录入因子</mu-sub-header> -->
          <!-- <mu-flex class="select-control-row">
            <mu-checkbox
              label="全选"
              :input-value="checkAll"
              @change="handleCheckAll"
              :checked-icon="this.checkbox.value1.length < 10 ? 'indeterminate_check_box' : undefined"
            ></mu-checkbox>
          </mu-flex>
          <mu-flex class="select-control-row" :key="index" v-for="(item,index ) in itemList">
            <mu-checkbox :value="item" v-model="checkbox.value1" :label="item" @change="chick"></mu-checkbox>
          </mu-flex>-->
          <!-- <mu-button full-width color="success" @click="()=>{open=false}">确定</mu-button> -->
          <div class="containerContBottom">
            <div v-for="(item,index) in itemList" :key="index" class="flexCheckbox">
              <van-checkbox-group v-model="result" ref="checkboxGroup">
                <van-checkbox :name="item" class="checkbox">{{item}}</van-checkbox>
              </van-checkbox-group>
            </div>
          </div>
          <!-- <van-button type="primary" @click="checkAll">全选</van-button>
          <van-button type="info" @click="toggleAll">反选</van-button>-->
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
  </div>
</template>
<script>
import Warning from "../../../static/js/warning.js";
import request from "../../../static/js/request.js";
import { Dialog } from "vant";
import "vant/lib/dialog/style";
import Vue from "vue";
import { Toast } from "vant";
Vue.use(Toast);
export default {
  name: "aboutMen",
  data() {
    return {
      token: "",
      itemList: [],
      token: "",
      openAlert: false,
      name: "",
      number: "",
      station: "",
      carPhone: "",
      carFactor: "",
      carPerson: "",
      open: false,
      checkbox: {
        value1: []
      },
      itemList: [
        "pH",
        "氨氮",
        "总氮",
        "总磷",
        "总铜",
        "总镍",
        "总氰化物",
        "总铬",
        "化学需氧量"
      ],
      checkAll: false,
      result: []
    };
  },
  methods: {
    // checkAll() {
    //   this.$refs.checkboxGroup.toggleAll(true);
    // },
    // toggleAll() {
    //   this.$refs.checkboxGroup.toggleAll();
    // },
    confirmSubmit() {
      var _this = this;
      Dialog.confirm({
        title: "请确认是否提交数据？",
        message: "驾驶人为：" + this.number
      })
        .then(() => {
          // on confirm
          _this.initData();
        })
        .catch(() => {
          // on cancel
          Toast("您点击了取消");
        });
    },
    selectDataPicPerson() {
      // this.opened = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classPicPerson, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.optionsperson, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择类型", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.person = selectArr[0];
          // _this.opened = true;
        }
      });
    },
    handleCheckAll() {
      this.checkAll = !this.checkAll;
      if (this.checkAll) {
        this.checkbox.value1 = this.itemList;
        this.carFactor = this.itemList;
      } else {
        this.checkbox.value1 = [];
        this.carFactor = [];
      }
    },
    chick(value) {
      this.carFactor = value;
    },
    closeBottomSheet() {
      this.open = false;
    },
    bottomTo() {
      this.open = true;
    },
    alertTo() {
      this.$router.go(-1);
    },
    openAlertDialog() {
      this.openAlert = true;
    },
    closeAlertDialog() {
      this.openAlert = false;
    },
    async initData() {
      this.openAlert = false;
      var name = this.name;
      var number = this.number;
      var station = this.station;
      var carPhone = this.carPhone;
      var carPerson = this.carPerson;
      var carFactor = this.result;
      var url = "/repertory/in-storage/vehicle";
      var res = await request.request(
        url,
        "POST",
        {
          name,
          number,
          station,
          carFactor,
          carPerson,
          carPhone
        },
        this.token
      );
      //   this.itemList = res.data.data;
      console.log(res);
      if (res.data.info === "车辆录入成功") {
        this.$router.go(-1);
        Toast(res.data.info);
      } else {
        Toast(res.data.info);
      }
    },
    submit() {
      this.openAlert = true;
    }
  },
  mounted() {
    this.token = sessionStorage.getItem("token");
    // this.initData();
  }
};
</script>
<style scoped>
.settingContent {
  width: 100%;
  text-align: center;
  padding: 10px;
  /* margin-top: 150px; */
}
.flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: 1px solid rgb(225, 239, 248);
  height: 50px;
  align-items: center;
}
#input {
  border: none;
  outline: none;
  border: 1px solid rgb(240, 240, 240);
  width: 100%;
  border-radius: 5px;
  padding: 0 5px;
  background: white;
  height: 30px;
}
.input {
  display: flex;
  flex: 1;
  padding: 0 10px;
}
.title {
  width: 80px;
}
.submit {
  width: 100%;
  background: #4ed995;
  height: 40px;
  font-size: 16px;
  border-radius: 10px;
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  /* box-shadow: 0 5px 0 rgb(231, 231, 231); */
}
.bottomBorder {
  border-bottom: #ccc 1px solid;
  font-size: 16px;
  font-weight: 600;
}
.checkbox {
  width: 100%;
}
.flexCheckbox {
  
  width: 30%;
  height: 40px;
}
.containerContBottom{
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content:space-between;
}
</style>



